
<template>
    <!--弹窗组件-->
  <iframe-choosen id="iframemodal"
                  ref="Choosen"
                  :parmDialog="parmDialog"
                  :echoData="echoData"
                  v-on:showMsgfromchild="showMsgfromchild"
                  v-on:sureSubmit="showMsgfromchild"
                  v-on:OpenDialog="OpenDialog"
                  v-on:CloseDialog="CloseDialog"
                  v-bind:Visible="Visible">
  </iframe-choosen>
</template>

<script>
  import vuex from 'vuex'
  import iframeChoosen from '@/components/biz/compositeChoosen.vue'
  export default {
    name: 'iframeDialogChoosen',
    components: {
        'iframeChoosen':iframeChoosen
    },
    data() {
      return {
        compName: '',
        childDataUrl: {url: ''},//传给子组件的值
        showdialog: false,
        closeShow: false,
        w:0,
        h:0,
        Visible:false,
        parmDialog:{
          typeIframe:'',//接收url是显示那个Tabs
          mutilselect:'',//单选多选
          classifier:'',
          formType:''//用于表单的iframe
        },
        echoData: [],
      }
    },
    /**
     * 外部iframe访问界面构造函数
     */
    created(){

    },
    mounted() {
      let _this = this;
      window.addEventListener('message',function(event) {
        if(event.data.key==='init'){
          var regExp = /\\/g;
          _this.echoData = [...event.data.data];
          for (let item of _this.echoData){
            let objkey = Object.keys(item);
            for(let key of objkey){
              if(item[key]){
               if(item[key].match(regExp)){
               item[key] = item[key].replace('\n','\\\\');
               console.log(item[key],9999)
              }
              }
            }
          }
        }
      },false);
      _this.parmDialog.classifier = _this.getUrlParam('compName');
      if(_this.parmDialog.classifier){
        _this.$store.state.initProgress.apiUrl = true
      }else {
        _this.$store.state.initProgress.apiUrl = true
      }
      _this.parmDialog.typeIframe = _this.getUrlParam('picktype');
      _this.parmDialog.formType = _this.getUrlParam('formType');
      if(_this.parmDialog.typeIframe==='' || null || undefined){
        _this.parmDialog.typeIframe = 'UserPick'
      }
      _this.parmDialog.mutilselect = _this.getUrlParam('mutilselect');
      _this.setDialog(_this.parmDialog.classifier);
      var okresult = {};
      okresult.data = _this.model;
      okresult.w = _this.w;
      okresult.h = _this.h;
      okresult.key='setCss';
      window.parent.postMessage(okresult,'*');
      _this.openDailog(_this.parmDialog.classifier);
    },
    methods: {
      setDialog(src){
        var width = 586;
        var height = 450;
        switch (src){
          case 'compsite-Choosen':
            width = 586;
            height = 450;
            break;
          default:
        }
        this.w = width;
        this.h = height;
      },
      // 获取对应url里面的属性值
      getUrlParam(param, href){
        let reg = new RegExp("[?&]"+param + "=([^&]*)", "i"); // 匹配目标参数，i表示区分大小写
        href = href || window.location.href;
        let result = href.match(reg); // result为数组，分别为全词，匹配的第一项（按括号区隔的），匹配的第二项……
        if (result != null) {
          return decodeURIComponent(result[1]);
        } else {
          return null;
        }
      },
      /**
       * 打开弹出框组件
       */
      openDailog(type) {
        // 弹出框类型
        if(type!==''){
          this.$refs.Choosen.openChoosen();
        }
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //循环遍历子组件传过来的数据
        let map  = {};
        let Data = [];
        for(let item of data){
          if(item.ProcessType==='ProcessPick'){
            Data.push({
                  Type:item.type,
                  CategoryId:item.categoryId,
                  CategoryName:item.categoryName,
                  ProcessCode:item.processCode,
                  ProcessName:item.processName,
                  VersionId:item.versionId,
                  VersionName:item.versionName,
                  ActivityId:item.activityId,
                  ActivityName:item.activityName,
                });
          }else {
            if(item.type==="Position" || item.type==="InventedPosition"){
              Data.push({
                'Type':item.type,
                'Value':item.UserAccount,
                'Name':item.UserDisplayName,
                'OrgPosMapSysId':item.OrganizationPositionMapSysId
              });
            }else {
              Data.push({
                'Type':item.type,
                'Value':item.UserAccount,
                'Name':item.UserDisplayName
              });
            }
          }
        }
        map.data = Data;
        map.key = 'ok';
        window.parent.postMessage(map,'*');
        console.log(map,9999)
      },
      // 点击关闭或者取消触发方法
      closeWin(data) {
        this.$refs.Choosen.close();
      },
      //关闭弹窗
      CloseDialog(val){
        var that = this;
        var okresult = {};
        okresult.key = 'close';
        okresult.data = {};
        that.Visible = val;
        window.parent.postMessage(okresult,'*');
      },
      //打开弹窗
      OpenDialog(val){
        var that = this;
        that.Visible = val;
        setTimeout(function () {
          let modal = document.querySelector(".v-modal");
          modal.style.background='#ffffff';
        },200)
      },
    }
  }
</script>
<style>
  #iframemodal .el-dialog{
    box-shadow: none!important;
    margin-bottom: 0!important;
  }
</style>




